<template>
  <v-app class="grey lighten-4">
    <v-toolbar app fixed color="blue darken-2">
      <v-toolbar-title class="white--text">{{todoTitle}}</v-toolbar-title>
      <v-spacer></v-spacer>
    </v-toolbar>
    <v-content>
      <v-tabs v-model="active" grow>
        <v-tabs-bar class="white navTab">
          <v-tabs-item href="#tab-1" ripple class="blue--text">
            未完成
          </v-tabs-item>
          <v-tabs-item href="#tab-2" ripple class="blue--text">
            已完成
          </v-tabs-item>
          <v-tabs-slider color="blue"></v-tabs-slider>
        </v-tabs-bar>
      </v-tabs>
      <v-tabs v-model="active" style="margin-top:50px">
        <v-tabs-items>
          <v-container fluid grid-list-md>
            <v-tabs-content id="tab-1">
              <v-layout row wrap>
                <draggable v-model="lists" class="draggableItem">
                  <v-flex v-for="list in lists" v-bind:key="list" xs12>
                    <v-card class="list-content" v-show="list.isFinished == false">
                      <v-layout row class="list-content-row">
                        <v-flex xs1 class="list-content-item">
                            <div 
                              class="priority-box" 
                              v-bind:class="[
                                {green:list.priority == 4},
                                {blue:list.priority == 3},
                                {amber:list.priority == 2},
                                {red:list.priority == 1}]"
                            ></div>
                        </v-flex>
                        <v-flex xs10 class="list-content-item" style="margin-left:-5%">
                          <span>
                            <v-icon 
                              color="blue" 
                              class="list-content-item" 
                              lg 
                              v-if="list.isFinished == false" 
                              xs1 
                              v-on:click="list.isFinished = !list.isFinished">
                              check_box_outline_blank
                            </v-icon>
                            <v-icon 
                              color="blue" 
                              class="list-content-item" 
                              lg 
                              v-if="list.isFinished == true" 
                              xs1 
                              v-on:click="list.isFinished = !list.isFinished">
                              check_box
                            </v-icon>
                          </span>
                          <span>
                            {{list.title}}
                          </span>
                        </v-flex>
                        <v-flex xs1 style="margin-left:5%">
                          <v-icon 
                            color="blue" 
                            class="list-content-item list-content-arrow" 
                            lg>
                            keyboard_arrow_right
                            </v-icon>
                        </v-flex>
                      </v-layout>
                    </v-card>
                  </v-flex>
                </draggable>     
              </v-layout>
            </v-tabs-content>
            <v-tabs-content id="tab-2">
              <v-layout row wrap>
                <draggable v-model="lists" class="draggableItem">
                  <v-flex v-for="list in lists" v-bind:key="list" xs12>
                    <v-card class="list-content" v-show="list.isFinished == true">
                      <v-layout row class="list-content-row">
                        <v-flex xs1 class="list-content-item">
                            <div 
                              class="priority-box" 
                              v-bind:class="[
                                {green:list.priority == 4},
                                {blue:list.priority == 3},
                                {amber:list.priority == 2},
                                {red:list.priority == 1}]">
                            </div>
                        </v-flex>
                        <v-flex xs10 class="list-content-item" style="margin-left:-5%">
                          <span>
                            <v-icon 
                              color="blue" 
                              class="list-content-item" 
                              lg 
                              v-if="list.isFinished == false" 
                              xs1 
                              v-on:click="list.isFinished = !list.isFinished">
                              check_box_outline_blank
                            </v-icon>
                            <v-icon 
                              color="blue" 
                              class="list-content-item" 
                              lg 
                              v-if="list.isFinished == true" 
                              xs1 
                              v-on:click="list.isFinished = !list.isFinished">
                              check_box
                            </v-icon>
                          </span>
                          <span class="finishedItem">
                            {{list.title}}
                          </span>
                        </v-flex>
                        <v-flex xs1 style="margin-left:5%">
                          <v-icon 
                            color="blue" 
                            class="list-content-item list-content-arrow" 
                            lg>
                            keyboard_arrow_right
                          </v-icon>
                        </v-flex>
                      </v-layout>
                    </v-card>
                  </v-flex>
                </draggable>     
              </v-layout>
            </v-tabs-content>
          </v-container>
        </v-tabs-items>
      </v-tabs>
    </v-content>
    <v-btn 
      fab 
      bottom 
      right 
      color="blue darken-2 white--text" 
      fixed 
      class="elevation-12" 
      v-on:click="inputBox = !inputBox">
      <v-icon>add</v-icon>
    </v-btn>
    <v-dialog v-model="inputBox" max-width="500px">
      <v-card>
        <v-card-title>
          <v-flex xs11> 添加待办事项</v-flex>
          <v-flex xs1>
            <v-icon v-on:click="inputBoxExtend = !inputBoxExtend">open_in_new</v-icon>
          </v-flex>
        </v-card-title>
        <v-card-text>
          <v-form v-model="valid" ref="form" lazy-validation>
            <v-text-field
              label="待办事项"
              v-model="input"
              :counter="20"
              :rules="inputRules"
              required
              v-on:enter="addNew">
            </v-text-field>
          </v-form>        
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey darken-1" v-on:click="inputBox = !inputBox" flat>取消</v-btn>
          <v-btn color="blue darken-2 white--text" v-on:click="addNew" :disabled="!valid">添加</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog 
      v-model="inputBoxExtend" 
      fullscreen
      transition="dialog-bottom-transition">
      <v-card>
        <v-card-text>
          <v-form v-model="valid" ref="form" lazy-validation>
            <v-text-field
              label="待办事项"
              v-model="input"
              :counter="20"
              :rules="inputRules"
              required
              v-on:enter="addNew">
            </v-text-field>
          </v-form>        
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey darken-1" v-on:click="inputBoxExtend = !inputBoxExtend" flat>取消</v-btn>
          <v-btn color="blue darken-2 white--text" v-on:click="addNew" :disabled="!valid">添加</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-app>
</template>

<script>
import draggable from 'vuedraggable'
import Store from "../store"
export default {
  components: {
    draggable,
  },
  name: 'main',
  
  data () {
    return {
      valid: false,
      active:false,
      inputBox:false,
      inputBoxExtend:false,
      todoTitle:'日程清单',
      input: "",
      lists: Store.fetch() == null ? [] : Store.fetch(),
      inputRules:[
        (v) => !!v || '请输入待办事项',
        (v) => v && v.length <= 20 || '不要超过20个字'
      ],
    }
  },
  watch: {
    lists: {
      handler(val, oldVal) {
        Store.save(this.lists);
      },
      deep: true
    }
  },
   methods: {
    addNew() {
      this.lists.push({
        title: this.input,
        isFinished: false,
        priority: 4
      });
      this.inputBox = !this.inputBox;
      this.input = "";
    }
  }
}
</script>

<style scoped>
.list-content{
  margin: 5px;
}
.list-content-item{
  line-height:50px;
}
.list-content-arrow{
  display: flex;
  justify-content: flex-end;
  margin-right: 5px;
}
.priority-box{
  width: 5px;
  height: 30px;
  margin-top: 10px;
}
.navTab{
  position: fixed;
  z-index: 1;
}
.draggableItem{
  width: 100%;
}
.finishedItem{
  color: #BCBCBC;
  text-decoration:line-through
}
</style>




